.schedule-btn-wrap {
	padding: clamp(100px, 5vw, 200px) 0;
	margin-top: -100px; }

.schedule {
	padding: 140px 0 0;
	margin: 0 0 80px;
	color: $black;

	@include laptop {
		padding: 120px 0 0; }
	@include sm {
		padding: 100px 0 0;
		margin: 0 0 50px; }

	.section {
		&__title {
			margin: 0;
			color: $textColor;
			@extend .section-title;
			@include bold;
			margin-bottom: 32px; } }

	&+.schedule {
		padding: 0px 0 0;

		.section__title {
 } }			// display: none

	.container {
		max-width: 1280px;
		@include xxs {
			padding: 0; } }

	&__content {
		padding: 60px 120px;
		background: $white;

		@include tablet {
			padding: 40px 80px; }


		@include md {
			padding: 10px 40px;
			margin: 0 -20px; }

		@include sm {
			padding: 0 0; } }

	&__tip {
		color: #888;
		margin: 12px 0 12px;
		@include xxs {
			padding: 0 20px; } }

	&__item {}

	&__tab-links {
		display: flex;
		align-items: center;
		margin-bottom: 0;
		align-items: stretch;
		@include md {
			margin-bottom: 0; } }
	&__tab-link {
		font-size: 20px;
		display: flex;
		flex: 1 1;
		align-items: center;
		text-align: center;
		color: #000;
		text-decoration: none;
		position: relative;
		margin: 0;
		padding: 15px 20px;
		color: $textColor;
		align-items: center;
		justify-content: center;
		cursor: pointer;
		transition: all ease 0.2s;
		letter-spacing: 0;
		line-height: 1.28;
		@include bold;
		@include first {
			margin: 0; }
		@include md {
			font-size: 20px;
			padding: 10px; }
		@include sm {
			font-size: 18px; }
		@include xs {
			font-size: 14px;
			padding: 10px 5px; }
		&:after {
			content: '';
			position: absolute;
			bottom: 0;
			left: 0;
			width: 100%;
			height: 1px;
			transition: 0.3s;
			background: $gray; }
		&.is-active {
			color: $themeColor;
			&:after {
				height: 2px;
				background: $themeColor;
				@include sm {
					height: 2px; } } } }

	&__tabs {
		@include xs {
			margin: 0; } }
	&__tab {
		display: none;
		&.is-active {
			display: block; } }

	&__btn {
		margin: 15px 0 0;

		@include tablet {
			padding: 5px 20px; } } }

.s-item {
	background: #fff;

	&__row {
		display: flex;
		@include sm {
			flex-wrap: wrap; }
		& + & {
			margin-top: 20px; } }
	&__col {
		padding: 16px 10px;
		@include md {
			padding: 10px 10px; }
		@include last {
			border: none; }
		&_d-sm-none {
			@include sm {
				display: none; } }
		&:nth-child(1) {
			text-align: center;
			width: 9.7%;
			@include sm {
				width: 80px;
				min-width: 80px;
				border: none;
 } }				// border-bottom: 1px solid #202421
		&:nth-child(2) {
			width: 26.3%;
			@include sm {
				//width: auto
				flex-grow: 1;
				padding-left: 0;
 } }				// border-bottom: 1px solid #202421
		&:nth-child(3) {
			width: 64%;
			@include sm {
				width: 100%;
				margin-top: 10px;
				margin: 0;
				padding-top: 0; } } }
	&__time {
		font-size: 24px;
		line-height: 1.42;
		margin-top: 4px;
		@include medium;
		color: $textColor;

		@include md {
			font-size: 18px; } }

	&__title {
		font-size: 22px;
		color: $textColor;
		a {
			color: $textColor;
			cursor: auto; }
		@include bold;
		@include md {
			font-size: 18px; }
		@include sm {
			font-size: 16px;
			margin: 2px 0; }
		& + .s-item__text {
			margin-top: 8px; }
		& + .s-item__company {
			margin-top: 8px;
			@include sm {
				margin-top: 5px; } }
		&_sm {
			font-size: 16px;
			margin: 18px 0 0;

			@include md {
				font-size: 16px; }

			@include sm {
				font-size: 14px;
				margin: 12px 0 0; } }

		&:first-child {
			margin-top: 0; } }


	&__text {
		letter-spacing: 0.3px;
		color: $black;
		line-height: 1.7;
		@include sm {
			color: $black; }
		img {
			float: left;
			width: 30%;
			margin-right: 24px; }
		a {
			color: $black; } }
	&__company {
		font-size: 14px;
		color: $black;
		@include sm {
			color: $black; } } }

.talks-list {
	.s-item {
		&__row {
			display: flex;
			@include sm {
				flex-wrap: wrap; }
			& + & {
				margin-top: 20px; } }
		&__col {
			padding: 16px 28px;
			border-right: 2px solid $gray;
			@include last {
				border: none; }
			&_d-sm-none {
				@include sm {
					display: none; } }

			&:nth-child(1) {
				text-align: center;
				width: 30%;
				@include sm {
					text-align: left;
					width: 100%;
					border: none;
					padding: 16px; } }
			&:nth-child(2) {
				width: 70%;
				@include sm {
					width: 100%;
					margin-top: 10px;
					margin: 0;
					padding: 16px;
					padding-top: 0; } } } } }


.talks {
	padding: 149px 0 97px;
	border-bottom: 1px solid #2c302d;
	@include laptop {
		padding: 55px 0 60px; }

	a {
	  color: #fff; }
	@include sm {
		border-bottom: none;
		padding-bottom: 20px; }
	.container {
		@include sm {
			padding: 0 10px; } }
	.s-item {
		background: transparent;
		border: none;
		max-width: 1200px;
		margin: 0 auto;
		@include first {
			margin: 0; }

		&__col {
			border: none;
			@include sm {
				padding: 0 20px;
				margin: 16px 0 0;
				@include first {
					margin: 0; } } }

		&__text {
			line-height: 1.6;
			margin-top: 10px;

			a {
				color: $themeColor; } }
		&__title {
			@include regular; } }

	.talk__item {
		margin-top: 6px;
		@include first {
			margin-top: 0; }
		@include sm {
			margin-top: 16px;
			@include first {
				margin-top: 0; } }
		@include xs {
			margin: 10px -10px;
			@include first {
				margin-top: 0; } } } }
