.hero {
	background-color: $themeColor;
	position: relative;
	//padding: 172px 0 348px
	display: flex;
	align-items: center;
	overflow: hidden;
	padding: 122px 0 288px;
	min-height: 100vh;
	.container {
		width: 100%; }
	@include tablet {
		padding: 140px 0 290px; }
	@include md {
		min-height: auto;
		display: block;
		padding: 85px 0 226px; }
	@include xs {
		padding: 80px 0 20px;
		&:before {
			content: '';
			position: absolute;
			right: 20px;
			top: 109px;
			width: 72px;
			height: 92px;
			background-image: url(../img/hero-dots-mob.svg);
			background-repeat: no-repeat;
			background-size: contain; } }
	&:after {
		content: '';
		background-color: $white;
		position: absolute;
		left: 0;
		bottom: 0;
		width: 100%;
		height: 30px;
		@include xs {
			display: none; } }
	&__inner {
		display: flex;
		justify-content: space-between;
		position: relative;
		z-index: 2;
		color: $white;
		@include xs {
			display: block; } }
	&__suptitle {
		font-size: 17px;
		line-height: 1.76;
		margin-bottom: 3px;
		@include md {
			font-size: 10px;
			margin-bottom: 14px;
			line-height: 1.5; } }
	&__title {
		@include bold;
		font-size: 44px;
		line-height: 1.36;
		margin-bottom: 76px;
		@include md {
			font-size: 28px;
			margin-bottom: 50px;
			line-height: 1.1; } }
	&__left {
		@include xs {
			margin-bottom: 254px; } }
	&__info {
		position: relative;
		@include xs {
			position: absolute;
			bottom: -20px;
			width: calc(100% + 40px);
			margin: 0 -20px;
			transform: translateY(100%);
			background-color: $white;
			color: $themeColor;
			padding: 47px 40px 53px; }
		&:before {
			content: '';
			position: absolute;
			left: -23px;
			bottom: 5px;
			width: 124px;
			height: 203px;
			background-image: url(../img/hero-dots.svg);
			background-repeat: no-repeat;
			background-size: contain;
			transform: translateX(-100%);
			@include md {
				display: none; }
			@include xs {
				display: block;
				background-image: url(../img/hero-dots-mob1.svg);
				width: 12px;
				height: 182px;
				left: 20px;
				top: -65px; } } }
	&__right {
		text-align: right; }
	&__date {
		@include semi;
		font-size: 28px;
		margin-bottom: 11px;
		line-height: 1.4;
		@include md {
			font-size: 14px;
			margin-bottom: 8px; } }
	&__place {
		font-size: 14px;
		line-height: 1.4;
		@include md {
			font-size: 10px; } }
	&__btn {
		margin-top: 120px;
		display: flex;
		flex-direction: column;
		gap: 10px;
		@include md {
			margin-top: 25px; }
		@include xs {
			margin-top: 20px; } }
	&__images {
		position: absolute;
		bottom: 0;
		left: 50%;
		transform: translateX(-50%);
		width: 1440px;
		min-width: 843px;
		@include tablet {
			width: 1250px; }
		@include md {
			width: 844px; } }
	&__moon {
		position: absolute;
		left: 50%;
		transform: translate(-50%, -6%);
		bottom: 0;
		z-index: -1;
		width: 47%;
		@include rh(780) {
			width: 40%; }
		@include xs {
			display: none; }
		img {
			width: 100%; } }
	&__back {
		width: 100%;
		@include xs {
			transform: translate(-5%, 33%); }
		img {
			display: block;
			width: 100%; } }
	&__front {
		position: absolute;
		left: 0;
		bottom: 0;
		width: 100%;
		@include xs {
			display: none; }
		img {
			display: block;
			width: 100%; } }
	&__hand {
		position: absolute;
		bottom: 0;
		left: 50%;
		transform: translateX(-43%) translateY(10%);
		width: 21%;
		z-index: 1;
		will-change: transform;
		overflow: hidden;
		@include rh(780) {
			width: 18%; }
		&.is-loaded {
			@include rmin(768) {
				animation: handIn 1s linear both; }
			.hero__hand-img {
				opacity: 1;
				transform: translate3d(0, 0, 0); }
			.hero__flash {
				&.flash {
					.flash__first {
						animation: flash ease-out 0.3s;
						animation-delay: 0.3s; }
					.flash__second {
						animation: flash ease-out 0.3s; } } }
			.hero__react {
				img {
					opacity: 1;
					transform: rotateY(0); } } }
		@include xs {
			transform: translate(-89%, 11%); }
		&__hand-img {
			display: block;
			width: 100%;
			opacity: 0;
			transform: translate3d(0,100%,0);
			transition: 0.6s; } }
	&__flash {
		position: absolute;
		top: 0;
		left: 13%;
		width: 85%;
		.flash__first, .flash__second {
			opacity: 0; }
		svg {
			display: block;
			width: 100%; } }
	&__react {
		width: 33%;
		position: absolute;
		top: 1%;
		left: 50%;
		transform: translateX(-50%);
		img {
			display: block;
			width: 100%;
			transition: 0.5s;
			transition-delay: 0.7s;
			transform: rotateY(180deg);
			opacity: 0; } }

	&_live {
		align-items: flex-start;
		height: 120px;
		min-height: 0;
		padding: 10px 0 10px;
		background: #230cea;

		&:after {
			display: none; }

		@include tablet {
			padding: 10px 0; }
		@include md {
			padding: 5px 0;
			height: 100px;
			margin-top: 55px; }
		@include sm {
			padding: 5px 0;
			height: 80px; }
		@include xs {
			height: auto;
			padding: 5px 0 !important; }

		.container {
			position: relative;
			z-index: 2;
			pointer-events: auto;
			width: 100%;
			height: 100%;
			padding: 0 80px;

			@include sm {
				padding: 0 20px; } }

		.hero {

			&__bg {
				z-index: 0;
				background: url(../img/screens-done-op.svg) center center;
				background-size: 840px;
				position: absolute;
				left: 0;
				top: 0;
				right: 0;
				bottom: 0; }

			&__inner {
				position: relative;
				width: 100%;
				height: 100%;
				padding: 0;
				margin: 0;
				display: flex;
				justify-content: space-between;

				@include md {
					margin: 0; }

				@include xs {
					flex-direction: column-reverse;
					align-items: center; } }

			&__date {
				font-size: 18px;
				margin: auto 0;

				@include md {
					font-size: 14px; }

				@include xs {
					font-size: 12px;
					margin: 15px 0 10px; } }

			&__logo {
				position: absolute;
				left: 50%;
				top: 50%;
				transform: translate(-50%, -50%);
				margin: 0;
				width: 80px;
				height: 100%;

				img {
					width: 100%; }

				@include md {
					width: 60px;
					margin: 0px auto 0; }

				@include xs {
					position: relative;
					left: 0;
					top: 0;
					transform: translate(0, 0);
					width: 50px;
					margin: 0px auto 0; } }

			&__partner {
				position: relative;
				height: auto;
				margin: auto 0 auto auto;
				top: 0;
				right: 0;
				font-size: 12px;

				@include tablet {
					font-size: 10px; }

				@include md {
					margin: auto 0 auto;

					img {
						height: 45px; } }

				@include xs {
					display: none; }

				img {
					height: 65px; } }

			&__partners {
				@include md {
					margin: 0; } }



			&__partner-text {
				@include sm {
					display: none; } } } } }

.h-list {
	@include md {
		font-size: 14px; }

	&_centered {
		justify-content: center; }

	&_min {
		max-width: 411px; }

	&__item {
		display: flex;
		@include md {
			line-height: 1.4; }
		br {
			display: none; }

		&:not(:last-child) {
			margin-bottom: 9px; } }

	&__value {
		margin-right: 19px;
		width: 38px;
		display: inline-block;
		@include semi;
		font-size: 20px;
		line-height: 1.4;
		@include md {
			font-size: 16px;
			margin-right: 9px; }
		@include xs {
			font-size: 14px; } } }

.hlist {
	@include md {
		font-size: 14px; }
	&__item {
		display: flex;
		@include md {
			line-height: 1.4; }
		br {
			display: none;
			@include md {
				display: block; } }
		&:not(:last-child) {
			margin-bottom: 9px; }
		span {
			margin-right: 19px;
			width: 38px;
			display: inline-block;
			@include semi;
			font-size: 20px;
			line-height: 1.4;
			@include md {
				font-size: 16px;
				margin-right: 9px; }
			@include xs {
				font-size: 14px; } } } }



@keyframes flash {
	from {
		opacity: 0; }
	52% {
		opacity: 0; }
	63% {
		opacity: 0.6; }
	74% {
		opacity: 0.3; }
	86% {
		opacity: 1; }
	to {
		opacity: 0; } }

@keyframes handIn {
	0% {
		transform: translateX(-43%) translateY(10%); }
	25% {
		transform: translateX(-43%) translateY(7.5%); }
	50% {
		transform: translateX(-43%) translateY(5%); }
	75% {
		transform: translateX(-43%) translateY(2.5%); }
	100% {
		transform: translateX(-43%) translateY(0%); } }
