.remote-switch {
  position: absolute;
  right: 40px;
  font-size: 12px;
  top: 16px;
  display: flex;
  height: 58px;
  align-items: center;
  padding: 0;
  background: transparent;
  pointer-events: auto;
  margin: 0 0 0 auto;
  border: 2px solid $themeColor;
  background: $themeColor;
  width: max-content;

  position: relative;
  top: 0;
  right: 0;
  margin: -20px 0 20px;

  @include md {
    height: 50px;
    margin: 10px auto 0 0;
    top: -15px; }

  @include sm {
    position: relative;
    top: 0;
    right: 0;
    margin: -20px 0 20px; }

  &__text {
    opacity: 0.6;
    font-size: 13px;
    text-decoration: none;
    color: $white;
    height: 100%;
    padding: 0 10px;
    display: flex;
    font-weight: 700;
    align-items: center;

    @include md {
      font-size: 12px; }

    &:first-child {
      padding: 0 10px 0 20px;

      @include md {
        padding: 0 10px 0 10px; } }

    &:last-child {
      padding: 0 20px 0 10px;

      @include md {
        padding: 0 10px 0 10px; } }


    &:not(.active):hover {
      color: $white;
      opacity: 1; }

    &.active {
      opacity: 1; } }

  &__switch {
    position: relative;
    width: 36px;
    height: 12px;
    border-radius: 6px;
    background: rgba(0, 0, 0, 0.2);
    cursor: pointer;

    @include md {
      width: 22px;
      height: 10px; }

    &:after {
      content: '';
      position: absolute;
      left: -3px;
      top: -3px;
      width: 18px;
      height: 18px;
      border-radius: 50%;
      background: $white;

      @include xs {
        width: 14px;
        height: 14px;
        top: -2px;
        left: -2px; } }

    &.active {
      &:after {
        left: auto;
        right: -3px;

        @include xs {
          left: auto;
          right: -2px; } } } } }
